//
// IMPORTS
//

@import "reset";
@import "variables";
@import "navbar";

// Syntax highlighting @import is at the bottom of this file

/**************/
/* BASE RULES */
/**************/

html {
  font-size: 100%;
	background-color: $white;
}

body {
	background: $white;
  font: 16px/1.4 $text;
  color: $darkGray;
	height: 100%;
	margin-left: 15%;
	margin-right: 15%;
	margin-top: 60px;
	text-align: justify;
	@media only screen and (max-width: 1000px) {
		margin-left: 0px !important;
		margin-right: 0 !important;
		margin-top: 0 !important;
		text-align: left !important;
		padding-left: 5px;
		padding-right: 5px;
	}
}

.container {
  margin: 0 auto;
  max-width: 740px;
  padding: 0 10px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $headings;
  color: $brown;
  font-weight: 200;
	border-bottom: 1px solid $brown;

  line-height: 1.7;
  margin: 1em 0 15px;
  padding: 0;

  @include mobile {
    line-height: 1.4;
  }
}

h1 {
  font-size: 30px;
  a {
    color: inherit;
  }
}

h2 {
  font-size: 24px;
}

h3 {
  font-size: 20px;
}

h4 {
  font-size: 18px;
  color: $gray;
}

p {
  margin: 15px 0;
}

a {
  color: $brown;
  text-decoration: none;
	cursor: pointer;
	font-weight: bold;
  &:hover, &:active {
    color: $gold;
  }
}

ul, ol {
  margin: 15px 0;
  padding-left: 30px;
}

ul {
  list-style-type: disc;
}

ol {
  list-style-type: decimal;
}

ol ul, ul ol, ul ul, ol ol {
  margin: 0;
}

ul ul, ol ul {
  list-style-type: circle;
}

em, i {
  font-style: italic;
}

strong, b {
  font-weight: bold;
}

img {
  max-width: 100%;
}

// Fixes images in popup boxes from Google Translate
.gmnoprint img {
  max-width: none;
}

.date {
  font-style: italic;
  color: $gray;
}

// Specify the color of the selection
::-moz-selection {
  color: $black;
  background: $wcuLightGray;
}
::selection {
  color: $black;
  background: $wcuLightGray;
}

// Nicolas Gallagher's micro clearfix hack
// http://nicolasgallagher.com/micro-clearfix-hack/
.clearfix:before,
.clearfix:after {
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

/*********************/
/* LAYOUT / SECTIONS */
/*********************/

//
// .masthead
//

.wrapper-masthead {
  margin-bottom: 50px;
}

.masthead {
  padding: 20px 0;
  @include mobile {
    text-align: center;
  }
}

.site-avatar {
  float: left;
  width: 70px;
  height: 70px;
  margin-right: 15px;

  @include mobile {
    float: none;
    display: block;
    margin: 0 auto;
  }

  img {
    border-radius: 5px;
  }
}

.site-info {
  float: left;

  @include mobile {
    float: none;
    display: block;
    margin: 0 auto;
  }
}

.site-name {
  margin: 0;
  color: $darkGray;
  cursor: pointer;
  font-family: $chem370display;
  font-size: 28px;
  letter-spacing: 1px;
	border-bottom: none !important;
}

.site-name a {
	font-weight: 200;
}

.site-description {
  margin: -5px 0 0 0;
  color: $gray;
  font-size: 16px;

  @include mobile {
    margin: 3px 0;
  }
}

