//
// .main
//

.posts > .post {
  padding-bottom: 2em;
  border-bottom: 1px solid $wcuLightGray;
}

.posts > .post:last-child {
  padding-bottom: 1em;
  border-bottom: none;
}

.post {
  blockquote {
    margin: 1.8em .8em;
    border-left: 2px solid $gray;
    padding: 0.1em 1em;
    color: $gray;
    font-size: 22px;
    font-style: italic;
  }

  .comments {
    margin-top: 10px;
  }

  .read-more {
    text-transform: uppercase;
    font-size: 15px;
  }
}

.wrapper-footer {
  margin-top: 400px;
	padding-top: 10px;
  // border-top: 1px solid #ddd;
  // border-bottom: 1px solid #ddd;
  background-color: $white;
}

footer {
  padding: 20px 0;
  text-align: center;
	color: lightgray;
	font-size: 14px;
}
.footer a {
	color: lightgray;
}
.footer a:hover {
	color: $brown;
}

.homepage-button {
	color: $brown;
	font-weight: normal;
	text-decoration: none;
	padding: 10px;
	margin: 3px;
	border: 1px solid $wcuLightGray;
	background-color: $veryLightGray;
	border-radius: 5px;
	display: block;
	text-align: center;
	&:hover {
		color: $gold;
		border: 1px solid $gold;
		border-radius: 5px;
	}
}

.card {
	border: 1px solid $wcuLightGray;
	background-color: $veryLightGray;
  border-radius: 5px; /* 5px rounded corners */
	padding-left: 5%;
	padding-right: 5%;
	padding-bottom: 5%;
}

.column {
  float: left;
  width: 50%;
	@media only screen and (max-width: 800px) {
    width: 100% !important;
}
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.b-menu {
  background-color: $white;
	@media only screen and (max-width: 1000px) {
		position: relative;
	}
}

// Settled on moving the import of syntax highlighting to the bottom of the CSS
// ... Otherwise it really bloats up the top of the CSS file and makes it difficult to find the start
@import "highlights";
